<template>
  <div class="hello">
    <h1>{{ this.title }}</h1>
    <div class="bgView">
      <h1 id="text">text</h1>
      <h1 class="content">{{ this.message }}</h1>
      <button class="button" @click="callNative()">点击我给原生传递事件和参数</button>
      <button class="button" @click="changeMessage()">修改颜色</button>
      <button class="button" @click="alertTishi('a')">Alert</button>

    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      title : 'Harmony OS与H5交互',
      message : '原生修改我的内容'
    }
  },

  methods: {

    callNative() {
      this.message = '修改中...'
      let param = {name: '赞歌',sex: '男',age: 18}
      window.ohosCallNative.callNative('10001', param, (data) => {
        let param = JSON.parse(data)
        this.message = param.data + param.callID
      })
    },
    changeMessage() {
      this.message = 'param'
      document.getElementById('text').style.color = 'green';
    },
    alertTishi(e) {
      console.log(e)
      alert(e)
    }
  },
  props: {
  },
  computed: {
  
  }
}
</script>

